.default-icon-favorites {
  @extend .default-icon-heart;
}

.default-icon-favorites-checked {
  @include getIcon("heartFill", $red-coral, 24);
}

.header-icon:hover .default-icon-favorites {
  @extend .default-icon-heartFill;
}

// Theme svg-icon buttons
// icon-width kommt aus theme-buttons
.btn {
  &.icon-left-favorites {
    @extend .icon-left-heart;
  }

  &.icon-left-favorites-checked::before {
    background-image: getIconUrl("heartFill", $red-coral, #{$icon-width});
    width: #{$icon-width}px;
    height: #{$icon-width}px;
  }
}

.icon-right-removeFavorites {
  @extend .icon-right-heartBroken;
}