$arrowHeight: calc(50% - 20px);

.category-slider {
  .category-button {
    .btn {
      @extend .mt-2;
      @extend .btn-dark-alt;
    }
  }

  .category-button-sm {
    @extend .btn-dark-alt;
  }

  .category-slides {
    @extend .px-5;
    @extend .mx-4;
  }

  .category-slider-next {
    @extend .p-1;
    @extend .bg-light;
    @extend .border;
    @extend .border-light;
    right: 0;
    top: $arrowHeight;

    .icon-category-slider-next {
      @extend .icon-chevronRight-dark-30;
    }
  }

  .category-slider-prev {
    @extend .p-1;
    @extend .bg-light;
    left: 0;
    top: $arrowHeight;

    .icon-category-slider-prev {
      @extend .icon-chevronLeft-dark-30;
    }
  }
}
